/* COMMON */
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* NAVBAR */
.nav {
  display: flex;
  background: #6200ee;

  align-items: center;
  justify-content: space-between;
}
.logo {
  color: #fff;
  font-size: 35px;
}
.logo-icons {
  font-size: 40px;
  color: #ece60e;
}
nav #user-dp {
  height: 40px;
}
nav .user {
  display: flex;
  align-items: center;
}
nav span {
  margin-left: 10px;
}
nav .right-nav {
  display: flex;
  color: #fff;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 22px;
}
nav .nav-links ul {
  display: flex;
  list-style-type: none;
}
nav .nav-links ul li {
  padding: 0 50px;
}
nav .nav-links ul li a {
  text-decoration: none;
  color: #fff;
}
nav .nav-links ul li a:hover {
  text-decoration: underline;
}
nav .nav-links ul li:hover {
  cursor: pointer;
}
.search-container {
  flex-grow: 0.5;
  /* padding: 0 20px; */
  position: relative;
  display: flex;
}
.search-container input {
  background: #ffffff;
  border: 1px solid #d0d0d0;
  box-sizing: border-box;
  border-radius: 3px;
  height: 40px;
  max-width: 60%;
  font-size: 16px;
  padding-left: 31px;
  box-sizing: border-box;
  flex-grow: 1;
}
.search-container input:focus {
  outline: none;
}
.search-icon {
  height: 20px;
  position: absolute;
  top: 10px;
  left: 7px;
}
.search-results {
  background: #fff;
  flex-grow: 1;
  border-radius: 0 0 3px 3px;
  position: absolute;
  width: 100%;
  top: 40px;
  border: 1px solid #e0e0e0;
  z-index: 100;
  max-height: 452px;
  overflow: scroll;
  display: none;
}
.search-results ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}
.search-results ul a {
  text-decoration: none;
  color: black;
}
.search-results-row {
  display: flex;
  align-items: center;
  padding: 9px 14px;
}
.search-results-row:hover {
  transform: scale(1.1);
  cursor: pointer;
}
.search-results-row img {
  height: 27px;
}
li .img-size {
  max-width: 60px;
  height: 60px;
}
li .img-size-profile {
  max-width: 80px;
  height: 80px;
}
#ECommerce {
  height: 200px;
  width: 200px;
}

* {
  box-sizing: border-box;
}

/* Add padding to containers */
.container {
  padding: 16px;
  background-color: white;
}

/* Full-width input fields */
input[type="text"],
input[type="password"] {
  width: 100%;
  padding: 15px;
  margin: 5px 0 22px 0;
  display: inline-block;
  border: none;
  background: #f1f1f1;
}

/* Overwrite default styles of hr */
hr {
  border: 1px solid #f1f1f1;
  margin-bottom: 25px;
}

/* Set a style for the submit button */
.registerbtn {
  color: white;
  padding: 5px 5px;
  margin: auto;
  border: none;
  cursor: pointer;
  width: 40%;
  opacity: 0.9;
}
.registerbtn button {
  width: 100%;
  background-color: #4caf50;
}

.registerbtn:hover {
  opacity: 1;
}

/* Add a blue text color to links */
a {
  color: dodgerblue;
}

/* Set a grey background color and center the text of the "sign in" section */
.signin {
  background-color: #f1f1f1;
  text-align: center;
}

.AddToMiddle {
  width: 40%;
  margin: auto;
  margin-top: 20px;
}
#Description {
  width: 500px;
  height: 200px;
  padding: 20px;
  margin: 20px;
  background: #f1f1f1;
}

.label {
  vertical-align: top;
}

/* Home.js */
.outer {
  width: 80%;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: auto;
}
.items {
  width: 90%;
  height: 20rem;
  margin: 10px;
  display: flex;
  justify-content: space-between;
  border: 1px solid black;
}

.items .left {
  align-self: start;
  width: 55%;
  display: flex;
  height: inherit;
}
.left .img {
  width: 55%;
  height: inherit;
}
.img img {
  width: 100%;
  height: inherit;
}
.left .data {
  width: 45%;
  height: inherit;
  padding: 10px;
}
.price {
  width: 100%;
  height: 50%;
  padding: 12px;
}
.rating {
  width: 80%;
  height: 50%;
  padding: 10px;
  display: flex;
  justify-content: flex-end;
}
.rating div {
  align-self: flex-end;
}

.items .right {
  align-self: end;
  width: 45%;
  height: inherit;
}
.product-description {
  width: 100%;
  height: 80%;
  font-size: 20px;
  padding: 10px;
}
.option-edit-delete {
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: space-between;
}
.increase-decrease {
  height: 40px;
  width: 40px;
  margin: 10px;
}

.qty {
  align-self: flex-start;
  font-size: 25px;
}
.button {
  border: none;
  outline: 0;
  padding: 10px;
  color: white;
  background-color: green;
  text-align: center;
  cursor: pointer;
  width: 150%;
  height: 50px;
  font-size: 18px;
}
.button:hover {
  opacity: 0.7;
}
.card-count {
  position: relative;
}
.count {
  width: 30px;
  height: 30px;
  border: 1px solid black;
  border-radius: 50%;
  position: absolute;
  top: -7px;
  right: 40px;
  padding: 5px;
  background-color: black;
  font-size: 20px;
}
cursor {
  cursor: pointer;
}
#button-color {
  border: none;
  outline: 0;
  padding: 10px;
  color: white;
  background-color: red;
  text-align: center;
  cursor: pointer;
  width: 150%;
  height: 50px;
  font-size: 18px;
}
.sort-price {
  position: fixed;
  top: 1;
  right: 0;
  font-size: 20px;
  border: none;
  outline: 0;
  padding: 10px;
  color: white;
  background-color: grey;
  text-align: center;
  cursor: pointer;
  width: 10%;
  height: 50px;
  font-size: 18px;
}

